.app {
    font-family: sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    height: 60vh;
    font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  }
  
  .signupform input[type="text"],
  .signupform input[type="password"] {
    height: 35px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    background-color: var(--yellow-color);
  }
  
  input[type="submit"], [type="button"] {
    margin-top: 10px;
    margin-bottom: 10px;
    cursor: pointer;
    font-size: 15px;
    background: #000000;
    color: #fff; 
    padding: 10px 20px;
    border-radius: 5px;
    border: 1px solid #000000;
    width: 300px;
  }
  
  input[type="submit"]:hover, [type="button"]:hover {
    background: var(--yellow-color);
  }
  
  .button-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .login-form {
    position: relative;
    background: #414141a3;
    box-shadow: 0 1px 2px 0 rgba(34,36,38,.15);
    margin: 1rem 0;
    padding: 2em 1em;
    border-radius: 15px;
    border: 1px solid rgba(34,36,38,.15);
  }
  
  .list-container {
    display: flex;
  }
  
  .error {
    color: red;
    font-size: 12px;
  }
  
  .title {
    text-align: center;
    font-size: 25px;
    margin-bottom: 20px;
    margin-top: 15px;
    color: #fffffff7;
  }
  
  .input-container {
    display: flex;
    flex-direction: column;
    width: 300px;
    gap: 8px;
    margin: 10px;
  }

  .input-color::placeholder{
    color: var(--placeholder-color);
    padding-left: 10px;
  }

  label{
    margin-bottom: 5px !important;
  }

  @media screen and (max-width: 1200px) {
    .input-container {
      width: 250px;
    }

    .login-form{
      margin-top: 200px;
    }
    .loginform input[type="text"], .loginform input[type="password"] {
      height: 35px;
      border: 1px solid rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      background-color: var(--yellow-color);
      width: auto;
    }
  }